.our-contacts {
    visibility: hidden;
}

.contacts {
    padding-top: 60px;
    background-image: 
    linear-gradient(
    to bottom,
    #ffffff 25%,
    #FFF5F6 75%
    );
}

.contacts-list {
    &:not(:last-child) {
        margin-bottom: 20px;
    }
}

.contacts-list__item {
    padding: 40px 32px;
    width: 280px;
    height: 412px;

    background-color: #FFFFFF;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;

    &:not(:last-child) {
        margin-bottom: 20px;
    } 
}

.contacts-place {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #F0D1A5;
    border-radius: 4px;
    width: 80px;
    height: 16px;
}

.contacts-place__another {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #C2E297;
    border-radius: 4px;
    width: 130px;
    height: 16px;

}

.contacts-place__type {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.04em;
    color: #FFFFFF;
}

.contacts-location {
    padding-top: 20px;
    padding-bottom: 24px;
    border-bottom: 1px solid #E1E1E1;
}

.contacts-location__city {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.04em;

    color: #000000;
}

.contacts-location__description {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.5;
    
    color: #907E82;
}

.contact-schedule {
    padding-top: 20px;
    padding-bottom: 24px;

    border-bottom: 1px solid #E1E1E1;
}

.contact-schedule__item {
    &:not(:last-child) {
        margin-bottom: 8px;
    }
}

.contact-schedule__days {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.04em;

    color: #000000;
}

.contact-schedule__hours {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.86;

    color: #FFA5BA;
}

.contacts-options {
    padding-top: 24px;
}

.contacts-options__item {
    &:not(:last-child) {
        margin-bottom: 8px;
    }
}

.contacts-options__link {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.5;

    color: #907E82;
    cursor: pointer;

    &:hover,
    &:focus {
        color: #D41443;
    }
}

