.customer-reviews {
  padding-top: 60px;
}
.reviews-slider__list {
  margin-bottom: 30px;
}
.customer-reviews__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.customer-reviews__imgs {
  margin-bottom: 30px;
}
.customer-reviews__wraper {
  margin-bottom: 30px;
  padding: 43px 50px 18px 50px;
  min-width: 280px;
  background-color: var(--background-customers);
  z-index: -1;
}
.customer-reviews__feedback {
  position: relative;
  font-weight: 500;
  font-size: 12px;
  line-height: 2.58;
  text-align: center;
  letter-spacing: 0.03em;
  color: var(--color-text-first);
  max-height: 250px;

  background: repeating-linear-gradient(
    to bottom,
    #fafafa,
    #fafafa 29px,
    #e1e1e1 29px,
    #e1e1e1 30px
  );
  z-index: 1;
}
.customer-reviews__feedback:before {
  content: '';
  position: absolute;
  display: block;
  top: -20px;
  left: -10px;
  width: 54px;
  height: 40px;
  background-image: url(../images/customer-quotes-mob.png);
  background-repeat: no-repeat;
  background-size: contain;
  z-index: -1;
}
.customer-reviews__location {
  margin-bottom: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.86;
  text-align: center;
  color: var(--color-text-first);
  &:before {
    content: '';
    display: block;
    margin: 0 auto 18px;
    text-align: center;
    width: 32px;
    height: 4px;
    background-image: url(../images/customer-tripledots-mob.png);
  }
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: transparent;
  background-image: url(../images/home-customer.png);
  background-repeat: no-repeat;
  background-size: contain;
}
