:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --transition-duration: 250ms;
  --background-site-main: #fff;
  --background-site-second: #fff5f6;
  --background-logo: #d41443;
  --background-btn1: #d41443;
  --background-btn2: #fff;
  --background-btn2-focus: #ecebeb;
  --background-cos: #fea5bb;
  --background-cos-focus: #d41443;
  --background-product1: #ffb8ca;
  --background-product2: #f0d1a5;
  --background-product3: #c2e297;
  --background-customers: #fafafa;
  --color-text-btn1: #fff;
  --color-text-btn2: #d41443;
  --color-text-header: #fff;
  --color-text-header-focus: #d41443;
  --color-title: #d41443;
  --color-title-pre: #ffa5ba;
  --color-title-pre-heder: #fff;
  --color-title-products: #fff;
  --color-text-first: #000;
  --color-text-second: #907e82;
  --color-text-time: #ffa5ba;
  --color-text-focus: #d41443;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
