.grid-list {
  width: 320px;
  height: auto;
  grid-column-gap: 2.53px;
  grid-row-gap: 1.89px;
  border-radius: 8px;
  grid-template-columns: repeat(7, 1fr);
  grid-auto-rows: minmax(43px, auto);
  margin: 0 auto;
  display: grid;
}

.grid-item:nth-child(1) {
  grid-row: 1 / 4;
  align-self: center;
}

.grid-item:nth-child(2) {
  grid-area: 1 / 2 / 3 / 2;
  align-self: center;
}

.grid-item:nth-child(3) {
  grid-area: 4 / 2 / 2 / 2;
  align-self: center;
}

.grid-item:nth-child(4) {
