  align-self: center;
}

.grid-item:nth-child(6) {
  grid-area: 1 / 5 / 3 / 5;
  align-self: center;
}

.grid-item:nth-child(7) {
  grid-area: 4 / 5 / 2 / 5;
  align-self: center;
}

.grid-item:nth-child(8) {
  grid-area: 3 / 6 / 1 / 6;
  align-self: center;
  margin-bottom: 20px;
}

.grid-item:nth-child(9) {
